                      placeholder="Country">
                    </div>
                    <div class="row">
                      <div class="col">
                        <div class="form-group">
                          <label for="contry" class="font-weight-medium pl-2">State</label>
                          <input type="text" class="form-control rounded-pill w3-light-grey border-0" id="contry" placeholder="State">
                        </div>
                      </div>
                      <div class="col">
                        <div class="form-group">
                          <label for="contry" class="font-weight-medium pl-2">City</label>
                          <input type="text" class="form-control rounded-pill w3-light-grey border-0" id="contry" placeholder="City">
