<script>
import { mapGetters } from 'vuex'
export default {
  data: () => ({
    copyUrl: '',
    imageUrl: '',
    form: {
      firstName: '',
      lastName: '',
      avatar: '',
      image: null,
      description: '',
      company: '',
      website: '',
      twitter: '',
      linkedin: '',
      facebook: '',
      youtube: '',
      address: {
        address: '',
        city: '',
        state: '',
        zip: '',
        country: ''
      },
      geolocation: {
        lat: '',
        lng: '',
      },
      account: '',
      paymentPlan: '',
    }
  }),
  computed: {
    ...mapGetters(['loading', 'error', 'user', 'language'])
  },
  watch: {
    imageUrl(value) {
      if (value) {
        return this.copyUrl = ''
      }
      return
    },
    copyUrl(value) {
      if (value) {
        return this.form.image = value
      }
      return
    }
  },
  methods: {
    onPhotoPicked(e) {
      const files = e.target.files
      let filename = files[0].name
      if (filename.lastIndexOf('.') <= 0) {
        // return this.error = 'Invalid file. Please, try again...'
        return this.language == 'fr' ? this.error = "Image invalide. Veuillez réessayer..." : this.error = 'Invalid image. Please, try again...'
      }
      const fileReader = new FileReader()
      fileReader.readAsDataURL(files[0])
      fileReader.addEventListener('load', () => {
        this.imageUrl = fileReader.result
      })
      this.form.image = files[0]
    },
    onPickPhoto () {
      this.$refs.photoInput.click()
    },
    onSaveProfile() {
      this.$store.dispatch('updateUser', this.form)
      this.$router.push('/')
    },
    getCurrentPosition () {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
          this.form.geolocation.lat = position.coords.latitude
          this.form.geolocation.lng = position.coords.longitude
        }, (error) => {
          switch(error.code) {
            case error.PERMISSION_DENIED:
              this.error = this.language == 'fr' ? "Vous avez refusé la demande de Géolocalisation." : "You denied the request for Geolocation."
              break;
            case error.POSITION_UNAVAILABLE:
              this.error = this.language == 'fr' ? "Les informations de Géolocalisation sont indisponibles." : "Location information is unavailable."
              break;
            case error.TIMEOUT:
              this.error = this.language == 'fr' ? "La demande d'obtention de votre Géolocation a expiré." : "The request to get user location timed out."
              break;
            case error.UNKNOWN_ERROR:
              this.error = this.language == 'fr' ? "Une erreur inconnue est survenue." : "An unknown error occurred."
              break;
          }
        });
      } else {
        this.error = this.language == 'fr' ? "Votre Géolocation a échoué..." : "Your Geolocation failed."
      }
    },
  },
  created() {
    this.getCurrentPosition()
  }
}
</script>

